<template>
  <div class="course_about_container">
    <div class="course_about_top">
      <div style="flex:1.3;margin-left:10px;font-size:18px;" v-on:click="backi">&lt;</div>
      <div style="flex:2;font-size:18px;">我的收藏</div>
    </div>
    <div class="course_about_content">
      <div style="width:100%;height:10px;"></div>
      <div class="course_collect_content_items" v-for="(item,index) in list" :key="index">
        <div class="course_collect_title">
          <div>{{item.title}}</div>
          <p style="float:right">
            <van-icon v-on:click="onClick(item)" name="star" color="orange" size="20" />
          </p>
        </div>
        <div class="course_collect_class">共{{item.section_num}}课时</div>
        <div class="course_collect_teacher">
          <img :src="item.teachersAvatar" class="course_collect_teacher_icon" alt />
          <span style="font-size:14px;color:grey">{{item.teachers}}</span>
        </div>
        <div class="course_collect_footer">
          <div style="color:grey">{{item.sales_num}}人已报名</div>
          <div style="color:orange">{{item.price | price}}</div>
        </div>
      </div>
      <div style="width:100%;text-align:center;color:grey;">没有更多了</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      
    };
  },
  methods: {
    onClick(item) {
      this.$http.put(`/api/app/collect/cancel/${item.collect_id}/1`).then(res => {
        this.$http.get("/api/app/collect?page=1&limit=10&type=1&").then(res => {
          window.console.log(res.data.data.list)
          this.list = res.data.data.list;
        })
      });
    },
    backi() {
      this.$router.back();
    }
  },
  mounted() {
    this.$http.get("/api/app/collect?page=1&limit=10&type=1&").then(res => {
      // window.console.log(res.data.data.list)
      this.list = res.data.data.list;
      // this.list.forEach(e => {
      //   this.id = e.collect_id;
      //   console.log(this.id);
      // });
    });
  },
  filters: {
    price(val) {
      if (val == 0) {
        return "0";
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    }
  }
};
</script>

<style>
.course_about_top {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  background: white;
}
.course_about_content {
  width: 100%;
  height: 600px;
  background: rgb(240, 240, 240);
  margin: 0 auto;
}
.course_collect_content_items {
  width: 94%;
  height: 180px;
  background: white;
  /* display: flex; */
  margin-top: 10px;
  margin-left: 3%;
  border-radius: 5px;
}
.course_collect_title {
  width: 100%;
  height: 40px;
}
.course_collect_class {
  width: 100%;
  height: 30px;
  font-size: 14px;
  color: grey;
}
.course_collect_teacher {
  width: 100%;
  height: 60px;
  line-height: 60px;
  /* background-color: rosybrown; */
}
.course_collect_teacher_icon {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  /* margin-top: 10px; */
  vertical-align: middle;
  margin: 0 10px;
}
.course_collect_footer {
  width: 100%;
  height: 50px;
  border-top: 1px solid rgb(235, 235, 235);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>